import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableContainer from "@mui/material/TableContainer";

const dispatchedEvents = [];

const SrsMuiTableContainer = function (props, ref) {
  return (
    <TableContainer {...props} theme={props.muiTheme}>
      <slot />
    </TableContainer>
  );
}

const styles = `
  /* Custom styles for the MuiTableContainer component */
`;

const props = [
  {
    name: "component",
    defaultValue: "div",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: ["srs-mui-table"],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiTableContainer), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
